import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController } from 'ionic-angular';
import { Profile } from '../../models/profile.interface';
import { User } from '../../models/user.interface';
import { LoadingServiceProvider } from '../../providers/loading-service/loading-service';
import { AuthServiceProvider } from '../../providers/auth-service/auth-service';
import { ToastServiceProvider } from '../../providers/toast-service/toast-service';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { LocalStorageServiceProvider } from '../../providers/local-storage-service/local-storage-service';
import { FlashProvider } from '../../providers/flash/flash';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  user: User = {
    username: '',
    password: '',
  } 

  public mask = [/[0]/, /\d/, /\d/, /\d/, ' ', /\d/, /\d/, /\d/, /\d/, ' ', /\d/, /\d/, /\d/, /\d/];
  validateErrors: any;

  page: string;

  private isUsernameValid: boolean = true;
  private isPasswordValid: boolean = true;

  constructor(
    public navCtrl: NavController, 
    public navParams: NavParams,
    public loading: LoadingServiceProvider,
    private auth: AuthServiceProvider,
    private viewCtrl: ViewController,
    private toast: ToastServiceProvider,
    private alert: AlertServiceProvider,
    private local: LocalStorageServiceProvider, 
    private flashProvider: FlashProvider
  ) {
  
  }

  ionViewDidLoad() {
    console.log('LoginPage');
  }


  onLogin(){
    if (this.validate()) {
      let loader = this.loading.show('loading ....');
      loader.present().then(() => {
        this.auth.AuthUser(this.user).subscribe(data => this.getSuccess(data, loader), error => this.getError(error, loader))
      });
    }
  }

  getError(error: any, l) {
    l.dismiss().then(() => {
      if (error.status == 422) {
        this.alert.presentAlert(null,error.json().error,'ok');
      } else if (error.status == 400) {
        this.flashProvider.show(error.json().error, 3500, 'danger');
      } else {
        this.toast.show('Server Error');
      }
    });
  }

  getSuccess(data: any, l){
    l.dismiss().then(() => {
      console.log('success');
      this.auth.setUser(data.token, data.data)
      this.local.saveTolocal('user',{
        username: this.user.username.replace(/\D/g, ''),
        password: this.user.password,
      });
      this.navCtrl.setRoot('HomePage');
    });  
  }
  
  validate(): boolean {
    this.isUsernameValid = true;
    this.isPasswordValid = true;

    if (!this.user.username || this.user.username.length == 0) {
      this.isUsernameValid = false;
    }

    if (!this.user.password || this.user.password.length == 0) {
      this.isPasswordValid = false;
    }

    return this.isPasswordValid && this.isUsernameValid;
  }
  

  register(){
    this.navCtrl.push('RegisterPage');
  }

}



